<template>
    <main class="content container">
      <div class="content__top content__top--catalog">
        <h1 class="content__title">
          Каталог
        </h1>
        <span class="content__info">
          152 товара
        </span>
      </div>

      <div class="content__catalog">
        <productFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo" :category-id.sync="filterCategoryId" :filter-color.sync="filterFilterColor" v-on:update-filters="goToFirstPage"/>

        <section class="catalog">
          <div v-if="productsLoading">Загрузка товаров...
          <div class="lds-default"><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div></div>
        </div>
          <div v-if="productsLoadingFailed">Произошла ошибка при загрузке товаров <button @click.prevent="loadProducts">Попробовать еще раз</button></div>

          <ProductList v-if="!productsLoading" :products="filteredProducts"/>  

          <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage" />  
        </section>      
      </div>
  </main> 
</template>

<script>

import ProductList from "@/components/ProductList.vue";
// import ProductItem from "@/components/ProductItem.vue";
import BasePagination from "@/components/BasePagination.vue";
import ProductFilter from "@/components/ProductFilter.vue";
import axios from 'axios';
import {API_BASE_URL} from "@/config";


export default {  
  components: { ProductList, BasePagination, ProductFilter },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterFilterColor: 0,  

      page: 1,
      productsPerPage: 3,
      loadState: true,

      productsData: null,

      productsLoading: false,
      productsLoadingFailed: false
    }
  },
  computed: { 
    filteredProducts() {
      let filteredProducts = this.products;
       if (this.filterColor) {
        filteredProducts = filteredProducts.filter((product) => product.colors.some((color) => color.id === this.filterColor));
      }
      return filteredProducts;
      },   
    // вывод изображения товаров с API
    products() {
        return this.productsData ? this.productsData.items.map((product) => ({
            ...product,
            image: product.image.file.url,
          }))
        : [];
      },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  }, 
  methods: {
    goToFirstPage() {
        this.page = 1;
      },
    loadProducts(){
      this.productsLoading = true;
      this.productsLoadingFailed = false;  
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios
          .get(API_BASE_URL + `/api/products`, {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo
            }
          })
          .then(response => this.productsData = response.data)
          .catch(() => this.productsLoadingFailed = true)
          .then(() => this.productsLoading = false);   
      }, 1000);                          
    }
  },
  watch: {
      page() {
        this.loadProducts();
      },
      filterPriceTo() {
          this.loadProducts();
      },
      filterPriceFrom() {
          this.loadProducts();
      },
      filterCategoryId() {
          this.loadProducts();
      }
  },
  created() {
    this.loadProducts();
  },
};
</script>

<style>
.lds-default {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-default div {
  position: absolute;
  width: 6px;
  height: 6px;
  background: #fcf;
  border-radius: 50%;
  animation: lds-default 1.2s linear infinite;
}
.lds-default div:nth-child(1) {
  animation-delay: 0s;
  top: 37px;
  left: 66px;
}
.lds-default div:nth-child(2) {
  animation-delay: -0.1s;
  top: 22px;
  left: 62px;
}
.lds-default div:nth-child(3) {
  animation-delay: -0.2s;
  top: 11px;
  left: 52px;
}
.lds-default div:nth-child(4) {
  animation-delay: -0.3s;
  top: 7px;
  left: 37px;
}
.lds-default div:nth-child(5) {
  animation-delay: -0.4s;
  top: 11px;
  left: 22px;
}
.lds-default div:nth-child(6) {
  animation-delay: -0.5s;
  top: 22px;
  left: 11px;
}
.lds-default div:nth-child(7) {
  animation-delay: -0.6s;
  top: 37px;
  left: 7px;
}
.lds-default div:nth-child(8) {
  animation-delay: -0.7s;
  top: 52px;
  left: 11px;
}
.lds-default div:nth-child(9) {
  animation-delay: -0.8s;
  top: 62px;
  left: 22px;
}
.lds-default div:nth-child(10) {
  animation-delay: -0.9s;
  top: 66px;
  left: 37px;
}
.lds-default div:nth-child(11) {
  animation-delay: -1s;
  top: 62px;
  left: 52px;
}
.lds-default div:nth-child(12) {
  animation-delay: -1.1s;
  top: 52px;
  left: 62px;
}
@keyframes lds-default {
  0%, 20%, 80%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.5);
  }
}
</style>

